@import '../../../scss/variables';

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  background-color: white;
  border-radius: 0.4em;

  &__content {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
  }

  &__image-wrapper {
    flex-basis: 50%;
    transform: scale(1.2) translateX(-40px);

    img {
      -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
      -moz-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
      box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
      max-width: 1000px;
      width: 100%;
    }
  }

  &__exit-btn {
    color: $text-color;
    border: none;
    font-size: 40px;
    background: none;
    cursor: pointer;
    display: block;
    max-width: 100px;
    position: absolute;
    right: 0;
    z-index: 1000;
  }

  &__text {
    padding: 1em;
    flex-basis: 50%;
  }

  &__inner {
    position: relative;
  }

  &__title {
    color: $text-color;
    margin-bottom: 15px;
  }

  &__btn {
    padding: 0.3em 1em;
    border: none;
    background-color: $color2;
    color: white;
    margin-top: 20px;
    cursor: pointer;
    font-size: 1.3rem;
    transition: 0.2s;

    &:hover {
      background-color: darken($color2, 5%);
    }
  }

  &__description {
    font-weight: 300;
  }
}
